#q2 {
    margin: 0;
    padding: 0;
    background-color: bisque;
}

.header {
    display: flex;
    justify-content: space-between;
}

.image-adjust {
    height: 100px;
    width: 100px;
    padding: 10px;
}

.header-container {
    display: flex;
    padding: 10px;
    background-color: bisque;
    justify-content: space-between;
}

.header-container ul {
    list-style-type: none;
    display: flex;
} 

.header-container ul li {
    height: 40px;
    width: 40px;
    padding: 5px;
    align-content: center;
}

.header-container ul li a {
    text-decoration: none;
    color: black;
    cursor: pointer;
}

.image-adjust {
    height: 60px;
    width: 60px;
}

.content-container {
     margin-top: 5px;
     background-color: beige;
}

.search-container {
    margin: 0 auto;
    padding: 10px;
    display: flex;
    width: 80%;
    background-color: rgb(211, 220, 226);
    justify-content: space-around;
}

.search-container .input-bar {
    flex: 1;
    max-width: 500px;
    height: 40px;
    border-radius: 10px;
    background-color: chartreuse;
}

.search-container .search-button {
    
    height: 40px;
    border-radius: 10px;
    background-color: orange;
}

.restaurants-container {
    display: flex;
    flex-wrap: wrap;
    margin-top: 5px;
}

.restaurantCard {
    border: 1px solid black;
    width: 200px;
    padding: 10px;
    margin-left: 10px;
    margin-right: 10px;
    margin-bottom: 10px;
    margin-top: 10px;
}


